<template>
  <div>{{ items }}</div>
  <button @click="getForm">get</button>
  <button @click="postForm">post</button>
</template>

<script>
export default {
  data() {
    return {
      items: '',
      name: '',
      email: 'email@email.com',
      password: 'pwd'
    }
  },
  setup() {},
  created() {},
  mounted() {
    console.log('')
    console.log('[MainComponent] : [mounted] : [start]')
    console.log('  : DOM  ')
    console.log('')

    // this.submitForm()
  },
  unmounted() {},
  methods: {
    getForm() {
      this.axios
        .get('/json1')
        .then((res) => {
          console.log(res)
          console.log('[MainComponent] : [axios] : [response]')
          console.log('  : ' + JSON.stringify(res))
          console.log('')
          this.items = res
        })
        .catch((error) => {
          console.log('')
          console.log('[MainComponent] : [axios] : [error]')
          console.log('  : ' + error.data)
          console.log('')
        })
        .finally(() => {
          console.log('')
          console.log('[MainComponent] : [axios] : [complete]')
          console.log('')
        })
    },
    postForm() {
      const formData = new FormData()
      formData.append('email', this.email)
      formData.append('password', this.password)
      formData.append('name', this.name)
      this.axios.post('/api/post1', formData).then((res) => {
        console.log(res.data)
      })
    }
  }
}
</script>
