<template>
  <img alt="Vue logo" src="../../assets/logo.png" />
  <h1>vue3-js-panel 2</h1>

  <button
    v-for="item in testPanelArray"
    :key="item.id"
    @click="triggerPanel(item)"
  >
    open {{ item.content }}
  </button>

  <JsPanel
    v-for="item in testPanelArray"
    :key="item.id"
    :visible="item.visible"
    :options="item.options"
    @close="item.visible = false"
  >
    <div>{{ item.content }}</div>
  </JsPanel>

  <Dialog
    header=" "
    :showHeader="true"
    :closable="false"
    v-model:visible="confirmVisible"
    :breakpoints="{ '960px': '75vw', '640px': '90vw' }"
    :style="{ width: '350px' }"
    :modal="true"
  >
    <div class="confirmation-content">
      <span>{{ runMsg }}</span>
    </div>
    <template #footer>
      <Button
        id="cancel"
        label=""
        icon="pi pi-times"
        class="p-button-text"
        ref="cancel"
        @click="cancel"
      />

      <Button
        id="ok"
        ref="ok"
        label=""
        icon="pi pi-check"
        class="p-button-text"
        @click="confirm"
      />
    </template>
  </Dialog>
</template>

<script>
import { reactive } from 'vue'
import { JsPanel } from 'vue3-js-panel'
import 'jspanel4/dist/jspanel.min.css'
import { ref } from 'vue'

export default {
  components: {
    JsPanel
  },
  data() {
    const self = this
    return {
      testPanelArray: [
        {
          id: 'a-content',
          content: 'A panel content',
          visible: false,
          options: {
            headerTitle: 'A Panel',
            boxShadow: 1,
            headerControls: {
              add: [
                {
                  html: '<span class="fas fa-cloud"></span>',
                  name: 'menu',
                  position: 1,
                  handler: (panel, control) => {
                    panel.content.innerHTML =
                      '<p>You clicked the "cloud" control</p>'
                  }
                },
                {
                  html: '<span class="fas fa-heart"></span>',
                  name: 'menu',
                  position: 1,
                  handler: (panel, control) => {
                    panel.content.innerHTML =
                      '<p>You clicked the "heart" control</p>'
                  }
                }
              ],
              minimize: 'remove',
              smallify: 'remove'
              // close: 'remove'
            }
          }
        },
        {
          id: 'b-content',
          content: 'B panel content',
          visible: false,
          options: {
            headerTitle: 'B Panel'
          }
        },
        {
          id: 'c-content',
          content: 'C panel content',
          visible: false,
          options: {
            headerTitle: 'C Panel',
            //onbeforeclose: (panel) => confirm('close')
            // onbeforeclose: (panel) => self.selfClose(panel)
            onbeforeclose: function (panel) {
              // return self.selfClose(panel) // .
              return confirm('close')
              // return false
            }
          }
        }
      ]
    }
  },
  mounted() {},
  unmounted() {},
  setup() {
    const confirmVisible = ref(false)
    const resolvePromise = ref(null)
    const runMsg = ref('')

    const confirm = () => {
      console.log('confirm')
      resolvePromise.value(true)
    }

    const cancel = () => {
      console.log('cancel')
      resolvePromise.value(false)
    }

    const show = async () => {
      confirmVisible.value = true
      runMsg.value = '?'
      console.log('showing...')
      return false
      /*
      return new Promise((resolve, reject) => {
        console.log('PromisePromise')
        resolvePromise.value = resolve
      })
      */
    }

    return { show, resolvePromise, confirm, cancel, confirmVisible, runMsg }
  },
  methods: {
    triggerPanel: (item) => {
      item.visible = true
    },
    async selfClose(panel) {
      console.log('sssss')
      // return this.show()
      return confirm('aaaa')
      // const yn = await this.createConfirm('close modal')
      // return confirm('close')
    },
    async createConfirm(message) {
      return new Promise((resolve, reject) => {
        this.runMsg = message
        this.displayConfirmationYN = true

        this.resolvePromise.value = resolve

        /*
        document
          .getElementById('ok')
          .addEventListener('click', () => complete(true), false)
        document
          .getElementById('#cancel')
          .addEventListener('click', () => complete(false), false)
          */

        /*
        $('#confirmMessage').text(message)

        $('#confirmYes').off('click')
        $('#confirmNo').off('click')

        $('#confirmYes').on('click', () => {
          $('.confirm').hide()
          complete(true)
        })
        $('#confirmNo').on('click', () => {
          $('.confirm').hide()
          complete(false)
        })

        $('.confirm').show()
        */
      })
    }
  }
}
</script>
