<template>
  <div>
    <h1></h1>
    <ModalView
      v-if="isModalViewed"
      @close-modal="isModalViewed = false"
      :no="1"
      :author="{ name: '', age: 111 }"
    >
      <ContentComponent msg="TEST" />
    </ModalView>
    <button @click="isModalViewed = true">Open Modal</button>

    <button @click="addPanel()">jspanel</button>

    <JsPanel
      v-for="item in panelList"
      :key="item.id"
      :visible="item.visible"
      :options="item.options"
      @close="item.visible = false"
      :is="item"
    >
      <div>{{ item.id }}</div>
    </JsPanel>
  </div>
</template>

<script>
import ContentComponent from '../../components/ContentComponent'
import ModalView from '../../components/ModalView'

import { JsPanel } from 'vue3-js-panel'
import 'jspanel4/dist/jspanel.min.css'

export default {
  components: {
    ContentComponent,
    ModalView,
    JsPanel
  },
  data() {
    return {
      isModalViewed: false,

      show: false,
      options: {
        animateIn: 'animated zoomIn faster',
        animateOut: 'animated zoomOut faster',
        headerTitle: 'Aesome Panel'
      },
      panelList: []
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    addPanel() {
      console.log(this.panelList.length)
      const panelDefault = {
        id: 'pan-id-',
        content: 'B panel content',
        visible: true,
        options: {
          headerTitle: 'B Panel',
          headerControls: {
            minimize: 'remove',
            smallify: 'remove'
            // close: 'remove'
          }
        }
      }
      panelDefault.id = 'pan-id-' + this.panelList.length
      panelDefault.options.headerTitle = 'pan-id-' + this.panelList.length
      this.panelList.push(panelDefault)
    }
  }
}
</script>
