<template>
  <!-- teleport:    . -->
  <teleport to="body">
    <!-- Modal   . -->
    <div class="backdrop" v-if="isVisible">
      <div class="modal-container">
        <!-- slot  BaseComponent . -->
        <slot></slot>
      </div>
    </div>
  </teleport>
</template>

<script>
import { ref } from 'vue'
export default {
  name: 'Modal',
  setup() {
    const isVisible = ref(false)

    //      .
    const open = () => {
      isVisible.value = true
    }

    const close = () => {
      isVisible.value = false
    }

    // setup  ,    .
    return {
      isVisible,
      open,
      close
    }
  },
  created() {
    console.log(this.isVisible)
  }
}
</script>

<style scoped>
.backdrop {
  z-index: 999;
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #efefef94;
}
.modal-container {
  background: white;
  max-width: 320px;
  width: 100%;
  padding: 1.5rem;
}
</style>
