<template>
  <Modal ref="baseModal">
    <div class="content-container">
      <p v-for="text in content" :key="text">
        {{ text }}
      </p>
    </div>
    <div class="buttons-container">
      <button class="btn confirm" @click="confirm"></button>
      <button class="btn cancel" @click="cancel"></button>
    </div>
  </Modal>
</template>

<script>
import Modal from './Modal.vue'
import { ref } from 'vue'

export default {
  name: 'ConfirmationModal',
  components: {
    Modal
  },
  //   .
  props: {
    content: Array
  },
  setup() {
    //     ref
    const baseModal = ref(null)
    // Promise    ref
    const resolvePromise = ref(null)

    const show = () => {
      // baseModal  .
      baseModal.value.open()
      console.log('showing...')
      // Promise  ,    / 
      //        .
      return new Promise((resolve, _) => {
        console.log('PromisePromise111...', resolvePromise.value)
        // resolve    .
        resolvePromise.value = resolve
        console.log('PromisePromise222...', resolvePromise.value)
      })
    }

    const confirm = () => {
      baseModal.value.close()
      resolvePromise.value(true)
    }

    const cancel = () => {
      baseModal.value.close()
      resolvePromise.value(false)
    }
    return { baseModal, show, confirm, cancel }
  }
}
</script>
