<template>
  <div>
    <!--
    <input type="search" v-model="searchText" @keyup="checkEnter($event)" />
    -->
    <input type="search" v-model="searchText" @keyup.enter="doSearch" />
    <button @click="doSearch"></button>
    <button type="submit" @click.prevent="doSearch"></button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchText: ''
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    doSearch() {
      console.log(this.searchText)
    },
    checkEnter(e) {
      if (e.keyCode === 13) {
        this.doSearch()
      }
    }
  }
}
</script>
