<template>
  <img alt="Vue logo" src="../../assets/logo.png" />
  <h1>vue3-js-panel</h1>

  <button
    v-for="item in testPanelArray"
    :key="item.id"
    @click="triggerPanel(item)"
  >
    open {{ item.content }}
  </button>

  <JsPanel
    v-for="item in testPanelArray"
    :key="item.id"
    :visible="item.visible"
    :options="item.options"
    @close="item.visible = false"
  >
    <div>{{ item.content }}</div>
  </JsPanel>
</template>

<script setup>
import { reactive } from 'vue'
import { JsPanel } from 'vue3-js-panel'
import 'jspanel4/dist/jspanel.min.css'

const testPanelArray = reactive([
  {
    id: 'a-content',
    content: 'A panel content',
    visible: false,
    options: {
      headerTitle: 'A Panel',
      boxShadow: 1,
      headerControls: {
        add: [
          {
            html: '<span class="fas fa-cloud"></span>',
            name: 'menu',
            position: 1,
            handler: (panel, control) => {
              panel.content.innerHTML = '<p>You clicked the "cloud" control</p>'
            }
          },
          {
            html: '<span class="fas fa-heart"></span>',
            name: 'menu',
            position: 1,
            handler: (panel, control) => {
              panel.content.innerHTML = '<p>You clicked the "heart" control</p>'
            }
          }
        ],
        minimize: 'remove',
        smallify: 'remove'
        // close: 'remove'
      }
    }
  },
  {
    id: 'b-content',
    content: 'B panel content',
    visible: false,
    options: {
      headerTitle: 'B Panel'
    }
  },
  {
    id: 'c-content',
    content: 'C panel content',
    visible: false,
    options: {
      headerTitle: 'C Panel'
    }
  }
])

const triggerPanel = (item) => {
  item.visible = true
}
</script>
